import React, { Component } from 'react'
import styled from 'styled-components'
import bg from './../assets/img/site-inline.png'
import pageBG from './../assets/img/menu-bg.jpg'


const Title = styled.h1`
  display: block;
  margin: 0 auto;
  width: 80%;
  font-size: 6.5em;
  letter-spacing: .25em;
  color: #FFF;
  text-shadow: 0px 0px 2px #000, 0px 0px 2px #000, 0px 0px 2px #000, 0px 0px 2px #000;
`
const TabBox = styled.ul`
  background: url(${bg});
  background-size: contain;
  background-repeat: no-repeat;
  background-position: right;
  margin: 0 auto;
  width: 80%;
  list-style: none;
`
const Tab = styled.li`
  display: flex;
  align-items: center;
  justify-contents: center;
  height: 3em;
  margin: 0 0 20px 0;
  &:hover{
    color: #FFB800;
    text-shadow: 0px 0px 2px #FFD800, 0px 0px 2px #FFD800, 0px 0px 2px #FFD800, 0px 0px 2px #FFD800;
  }
`
const TabTail = styled.span`
  position: relative;
  background: #5E0000;
  border: 3px solid #8E0000;
  border-style: solid none solid solid;
  display: block;
  left: 3px;
  height: 50%;
  width: 6em;
  z-index: 5;
`
const Name = styled.strong`
  border: 3px solid #8E0000;
  background: #5E0000;
  font-size: 2.3em;
  color: #FFF;
  border-radius: 0 3em 3em 0;
  width: 10em;
  padding: 7.5px;
  text-align: center;
  text-transform: capitalize;
  text-shadow: 0px 0px 2px #000, 0px 0px 2px #000, 0px 0px 2px #000, 0px 0px 2px #000;
`
const Info = styled.section`
  background: #5E0000;
  border: 3px solid #8E0000;
  color: #FFF;
  text-shadow: 2px 2px 2px #000, 2px 2px 4px #000;
  font-size: 2em;
  width: 80%;
  margin: 15px auto 0;
  padding: 15px;
  height: 100px;
`

class NavMenu extends Component {
  constructor(props){
       super(props)
       // seems to bind a function to a specific... thingy
       // this.hoverState is the VARIABLE you should be using; do not add ''()''
       this.hoverState = this.hoverState.bind(this)
       this.state = {
          data: this.info,
          tab: [
            'file',
            'power',
            'arms',
            'guns',
            ''
          ],
          currentHoverState: ''
       }
    }
    info = [
         'Defy the destiny of dark blood.',
         'Surpass those who came before.',
         'Conquer and command Hell itself.',
         'Learn the tools of the trade.',
         'Awaken to the Reason.'
       ]

    hoverState(e){
      e = e.target.innerText
      let text = ''

      switch (e){
        case 'File':
          text = this.state.data[0]
        break
        case 'Power':
          text = this.state.data[1]
        break
        case 'Arms':
          text = this.state.data[2]
        break
        case 'Guns':
          text = this.state.data[3]
        break
        case '':
          text = this.state.data[4]
        break
        default:
          return null
      }
      // works technically as intended but keeps updating <li> too. need to fix that somehow
      return this.setState(
        {currentHoverState: text}
      )
    }

    render() {
      return (
        <div
          className='component_container'
          style={{
            background: `url(${pageBG})`,
            backgroundSize: 'cover',
            backgroundRepeat: 'no-repeat',
            backgroundPosition: 'center',
        }}>
          <Title>Status</Title>
          <TabBox>
            { this.state.tab.map(
            (nav, i) => {
              return <Tab key={i + (Math.random() ** 2)}
              >
              <TabTail/>
              <Name
              key={i + (Math.random() ** 2)}
              // get stringified text here; triggers onmouseover
              onMouseOver={this.hoverState}
              >
              { nav }
              </Name>
              </Tab>
            }) }
          </TabBox>
          <Info>
            <p>{this.state.currentHoverState}</p>
          </Info>
        </div>
      )
  }
}
export default NavMenu;



